import { FetchError, getBackendSrv } from '@grafana/runtime';
import { t } from 'i18next';
import { lastValueFrom } from 'rxjs';

import { Action, EditActionData, ErrorResponse } from '../types';
import { ApiName, getApiMessage, notifyError } from '../utils';
import { BaseApi } from './base';

/**
 * Actions Api
 */
export class Actions extends BaseApi {
  /**
   * Get Actions
   */
  async getAll() {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Action[]>({
        url: `${this.api.instanceSettings.url}/actions`,
        method: 'GET',
      })
    ).catch((e) => {
      notifyError([t('api.error'), getApiMessage(ApiName.GET_ACTIONS)]);
      throw e;
    });

    if (!response) {
      return [];
    }

    return response.data;
  }

  /**
   * Create action
   */
  async create<T extends EditActionData>({ id, ...action }: Exact<T, EditActionData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Action>({
        url: `${this.api.instanceSettings.url}/actions`,
        method: 'POST',
        data: action,
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.CREATE_ACTION, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Update Action
   */
  async update<T extends EditActionData>(id: string, { id: notUsedId, ...action }: Exact<T, EditActionData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Action>({
        url: `${this.api.instanceSettings.url}/actions/${id}`,
        method: 'PUT',
        data: action,
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.UPDATE_ACTION, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Delete action
   * @param id
   */
  async delete(id: string) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Action>({
        url: `${this.api.instanceSettings.url}/actions/${id}`,
        method: 'DELETE',
      })
    ).catch((e: FetchError<ErrorResponse>) => {
      notifyError([t('api.error'), getApiMessage(ApiName.DELETE_ACTION, e.data?.code ? e.data.code : undefined)]);
      throw e;
    });

    return response.data;
  }

  /**
   * Get Default Action
   */
  async getDefault() {
    const response = await lastValueFrom(
      getBackendSrv().fetch<Action>({
        url: `${this.api.instanceSettings.url}/actions/default`,
        method: 'GET',
      })
    ).catch(() => {});

    if (!response) {
      return null;
    }

    return response.data;
  }

  /**
   * Test Action
   */
  async test<T extends EditActionData>({ id, ...action }: Exact<T, EditActionData>) {
    const response = await lastValueFrom(
      getBackendSrv().fetch<{ ok: boolean; message: string }>({
        url: `${this.api.instanceSettings.url}/actions/test`,
        method: 'POST',
        data: action,
      })
    ).catch((e: FetchError<{ ok: boolean; message: string }>) => {
      return e;
    });

    return response.data;
  }
}
